import React, { useState } from 'react';
import CompactToolPanel from '../Tools/CompactToolPanel';
import ObjectPanel from '../Tools/ObjectPanel';
import ImageUpload from '../Upload/ImageUpload';

const FloatingToolbar = ({ 
  step,
  currentTool,
  onToolChange,
  selectedColor,
  onColorSelect,
  brushSize,
  setBrushSize,
  currentBrushType,
  setCurrentBrushType,
  selectedObjectType,
  onObjectSelect,
  onImageUpload,
  backgroundImage,
  setBackgroundImage,
  setUploadedImage,
  setStep,
  onSaveProject,
  onClearCanvas
}) => {
  const [isToolsOpen, setIsToolsOpen] = useState(false);
  const [isObjectsOpen, setIsObjectsOpen] = useState(false);
  const [isHelpOpen, setIsHelpOpen] = useState(false);

  return (
    <>
      {/*   */}
      <div style={{
        position: 'fixed',
        top: '20px',
        left: '20px',
        display: 'flex',
        gap: '10px',
        zIndex: 1000
      }}>
        {/*   */}
        {step === 'edit' && (
          <button
            onClick={() => setIsToolsOpen(!isToolsOpen)}
            style={{
              width: '50px',
              height: '50px',
              borderRadius: '25px',
              border: 'none',
              backgroundColor: isToolsOpen ? '#4CAF50' : 'white',
              color: isToolsOpen ? 'white' : '#333',
              fontSize: '20px',
              cursor: 'pointer',
              boxShadow: '0 2px 10px rgba(0,0,0,0.2)',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center'
            }}
          >
            
          </button>
        )}
        
        {/*   */}
        {step === 'edit' && (
          <button
            onClick={() => setIsObjectsOpen(!isObjectsOpen)}
            style={{
              width: '50px',
              height: '50px',
              borderRadius: '25px',
              border: 'none',
              backgroundColor: isObjectsOpen ? '#FF9800' : 'white',
              color: isObjectsOpen ? 'white' : '#333',
              fontSize: '20px',
              cursor: 'pointer',
              boxShadow: '0 2px 10px rgba(0,0,0,0.2)',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center'
            }}
          >
            
          </button>
        )}



        {/*   */}
        {step === 'edit' && (
          <button
            onClick={onSaveProject}
            style={{
              width: '50px',
              height: '50px',
              borderRadius: '25px',
              border: 'none',
              backgroundColor: 'white',
              color: '#333',
              fontSize: '20px',
              cursor: 'pointer',
              boxShadow: '0 2px 10px rgba(0,0,0,0.2)',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center'
            }}
          >
            
          </button>
        )}

        {/*   */}
        {step === 'edit' && (
          <button
            onClick={onClearCanvas}
            style={{
              width: '50px',
              height: '50px',
              borderRadius: '25px',
              border: 'none',
              backgroundColor: 'white',
              color: '#333',
              fontSize: '20px',
              cursor: 'pointer',
              boxShadow: '0 2px 10px rgba(0,0,0,0.2)',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center'
            }}
          >
            
          </button>
        )}

        {/*   */}
        <button
          onClick={() => setIsHelpOpen(!isHelpOpen)}
          style={{
            width: '50px',
            height: '50px',
            borderRadius: '25px',
            border: 'none',
            backgroundColor: isHelpOpen ? '#2196F3' : 'white',
            color: isHelpOpen ? 'white' : '#333',
            fontSize: '20px',
            cursor: 'pointer',
            boxShadow: '0 2px 10px rgba(0,0,0,0.2)',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center'
          }}
        >
          
        </button>
      </div>

      {/*   */}
      {isToolsOpen && step === 'edit' && (
        <div style={{
          position: 'fixed',
          top: '80px',
          left: '20px',
          backgroundColor: 'white',
          borderRadius: '10px',
          boxShadow: '0 4px 20px rgba(0,0,0,0.15)',
          zIndex: 999,
          minWidth: '250px'
        }}>
          <CompactToolPanel
            currentTool={currentTool}
            onToolChange={onToolChange}
            selectedColor={selectedColor}
            onColorSelect={onColorSelect}
            brushSize={brushSize}
            setBrushSize={setBrushSize}
            currentBrushType={currentBrushType}
            setCurrentBrushType={setCurrentBrushType}
            selectedObjectType={selectedObjectType}
            onObjectSelect={onObjectSelect}
          />
        </div>
      )}
      
      {/*   */}
      {isObjectsOpen && step === 'edit' && (
        <div style={{
          position: 'fixed',
          top: '80px',
          left: '80px',
          backgroundColor: 'white',
          borderRadius: '10px',
          boxShadow: '0 4px 20px rgba(0,0,0,0.15)',
          zIndex: 999,
          minWidth: '250px'
        }}>
          <ObjectPanel
            currentTool={currentTool}
            onToolChange={onToolChange}
            selectedObjectType={selectedObjectType}
            onObjectSelect={onObjectSelect}
          />
        </div>
      )}

      {/*    */}
      {step === 'edit' && backgroundImage && (
        <div style={{
          position: 'fixed',
          top: '20px',
          right: '20px',
          zIndex: 1000
        }}>
          <button
            onClick={() => {
              setBackgroundImage(null);
              setUploadedImage(null);
              setStep('upload');
            }}
            style={{
              width: '50px',
              height: '50px',
              borderRadius: '25px',
              border: 'none',
              backgroundColor: 'white',
              color: '#333',
              fontSize: '20px',
              cursor: 'pointer',
              boxShadow: '0 2px 10px rgba(0,0,0,0.2)',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center'
            }}
          >
            
          </button>
        </div>
      )}

      {/*   */}
      {isHelpOpen && (
        <div style={{
          position: 'fixed',
          bottom: '20px',
          left: '20px',
          backgroundColor: 'white',
          borderRadius: '10px',
          boxShadow: '0 4px 20px rgba(0,0,0,0.15)',
          zIndex: 999,
          padding: '15px',
          maxWidth: '300px'
        }}>
          <h4 style={{ margin: '0 0 10px 0', fontSize: '14px' }}></h4>
          {step === 'upload' && (
            <ul style={{ margin: 0, paddingLeft: '20px', fontSize: '12px' }}>
              <li>1:    </li>
              <li>2: 76    </li>
              <li>3: 11296   </li>
            </ul>
          )}
          {step === 'edit' && (
            <ul style={{ margin: 0, paddingLeft: '20px', fontSize: '12px' }}>
              <li>   </li>
              <li>  / </li>
              <li>  </li>
              <li> +   </li>
              <li>   /</li>
            </ul>
          )}
        </div>
      )}

      {/*      */}
      {isHelpOpen && (
        <div
          style={{
            position: 'fixed',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            zIndex: 998
          }}
          onClick={() => {
            setIsHelpOpen(false);
          }}
        />
      )}
    </>
  );
};

export default FloatingToolbar;